<template>
  <div>
    <div class="header">
      <img src="../assets/logo.png" alt="" class="logo">
      政企智慧服务平台
    </div>
    <div class="body">
      <div class="body-l">
        <img src="../assets/index.png" alt="">
      </div>
      <div class="body-r">
        <div class="login">
          <span class="login-1">登录</span>
          &nbsp;&nbsp;
          <span @click="toRegister" style="cursor:pointer;">注册</span>
        </div>
        <div class="account">
          <input type="text" placeholder="请输入用户名或手机号" v-model="account">
          <img src="../assets/people.png" alt="">
        </div>
        <div class="password">
          <img src="../assets/block.png" alt="">
          <input type="password" placeholder="" v-model="password" ref="password">
          <img src="../assets/eye.png" alt="" class="eye" id="eye" @click="togglePassword">
        </div>
        <p class="notice" style="height: 19px"></p>
        <input type="button" value="登陆" class="loginButton" @click="toLogin" style="cursor: pointer" ref="login">
        <div class="forgetPassword">
          <span style="cursor: pointer" @click="$router.push('/forgetPW')">忘记密码</span>
        </div>
      </div>
    </div>
    <div class="footer">
      <div>
        <span>关于本站</span>
        <span>版权声明</span>
        <span>常见问题</span>
      </div>
      <div>
        <span>版权所有：国家高新技术产业开发区管委会 主办方：国家高新区大数据局</span>
      </div>
      <div>
        <span>网站备案号：皖ICP备10207486号 　网站标识码：3708000041</span>
      </div>
      <div>
        <span>党政机关网站标识号：CA162410000604238070001        皖公网安备 37089702000262号</span>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "Login",
        data(){
            return {
                account:'',
                password:'',
                passwordSign:true
            }
        },
        methods:{
            toLogin(){
                var that = this;
                this.$refs.login.setAttribute('disabled','disabled');
                var that =this;
                if (this.account==''){
                    this.$message({
                        message: '请输入用户名',
                        type: 'warning'
                    });
                    return false
                }
                if (this.password==''){
                    this.$message({
                        message: '请输入密码',
                        type: 'warning'
                    });
                    return false
                }
                this.$axios.post('http://192.168.30.43:8080/user/userLogin', this.$qs.stringify({
                    userName:this.account,
                    password:this.password
                })).then(function(res) {
                    that.$refs.login.removeAttribute('disabled')
                    if (res.data.code == 0){
                        that.$message({
                            message: '登陆成功',
                            type: 'success'
                        });
                        console.log(res.data.data)
                        that.$cookies.set('username',res.data.data);
                    }else {
                        that.$message.error(res.data.msg)
                    }
                })
                    .catch(err=>{

                    })


            },
            toRegister(){
                this.$router.push('/register');
            },
            //切换密码
            togglePassword(){
                var password = this.$refs.password;
                if (this.passwordSign){
                    password.type="text";
                    this.passwordSign=false;
                }else  {
                    password.type="password";
                    this.passwordSign=true;
                }

                console.log(password)
            }
        }
    }
</script>

<style scoped>
  *{
    margin: 0;
    padding: 0;
  }
  .header {
    height: 60px;
    background-color: #4382ff;
    font-size: 18px;
    color: #ffffff;
    font-weight: 600;
    line-height: 60px;
    padding-left: 72px;
    position: relative;
  }
  .logo {
    position: absolute;
    left: 31px;
    top: 50%;
    transform: translateY(-50%);
  }
  .footer {
    width: 100%;
    min-width: 1025px;
    height: 133px;
    background-color: rgba(3, 87, 255, 0.749019607843137);
    font-size: 12px;
    color: #FFFFFF;
    margin-top: 10px;
    box-sizing: border-box;
  }
  .footer> div{
    text-align: center;
  }
  .footer>div:nth-child(1) {
    text-align: center;
    padding-top: 35px;
    padding-bottom: 19px;
  }
  .footer>div:nth-child(1) span:nth-child(1){
    padding-right: 115px;
  }
  .footer>div:nth-child(1)span:nth-child(3){
    padding-left: 115px;
  }
  .body {
    width: 1024px;
    margin: 0 auto;
    height: 594px;
    box-sizing: border-box;
    padding-top: 99px;
    display: flex;
    position: relative;
  }
  .body-l {
    position: absolute;
    left: 45px;
    top: 99px;
  }
  .body-r {
    padding-left: 666px;
    padding-top: 48px;
  }
  .login span:nth-child(1){
    font-size: 18px;
    color: rgba(3, 87, 255, 0.749019607843137);
    font-weight: 650;
    display: inline-block;
    position: relative;
  }
  .login span:nth-child(2) {
    font-size: 18px;
    color: #333333;
    font-weight: 650;
    position: relative;
    display: inline-block;
  }
  .login-1:after {
    content: '';
    display: inline-block;
    width: 1px;
    height: 28px;
    background-color: #797979;
    position: absolute;
    right: -12px;
    top: 0;
  }
  .login {
    margin-bottom: 30px;
  }
  .account input {
    width: 300px;
    height: 47px;
    border: 1px solid #797979;
    box-sizing: border-box;
    padding-left: 51px;
  }
  .account {
    position: relative;
  }
  .account img {
    position: absolute;
    left: 10px;
    top: 7px;
  }
  .password {
    position: relative;
  }
  .password input{
    width: 300px;
    height: 47px;
    border: 1px solid #797979;
    box-sizing: border-box;
    padding-left: 51px;
  }
  .password img:nth-child(1) {
    position: absolute;
    left: 10px;
    top: 7px;
  }
  .password {
    margin-top: 24px;
  }
  .eye {
    position: absolute;
    right: 15px;
    top: 13px;
  }
  .notice {
    font-size: 14px;
    color: #D9001B;
    font-weight: 400;
    margin-top: 3px;
    margin-bottom: 8px;
  }
  .loginButton {
    width: 300px;
    height: 47px;
    border-radius: 3px;
    border: none;
    outline: none;
    background-color: rgba(3, 87, 255, 0.749019607843137);
    font-size: 20px;
    color: #FFFFFF;
  }
  .forgetPassword {
    color: #333333;
    font-size: 14px;
    width: 100%;
    padding-left: 242px;
    margin-top: 15px;
  }
</style>



